<script>
  import vueBarcode from 'vue-barcode'
  import CONFIG from '../config'
  import {mapState} from 'vuex'
  import joPay from './wxpay'
  import {MessageBox, Swipe, SwipeItem} from 'mint-ui'
  import quanprogress from './quanprogress.vue'

  export default {
    data() {
      return {
        start_desc: '投币启动',
        gameNum: 1,
        is_start: false,
        is_lamp_after: false,
        is_recharge: false,
        loading_art: '加载更多~',
        load: false,
        max_page: false,
        maskShow: false,
        isShow: '',
        showHtml: false,
        bgShow: false,
        contentShow: '',
        currentCoupon: {},
        pageIndex: 1,
        cinemaInfo: {
          name: '北京耀莱影城马连道店',
          price: 30,
          goodsName: '46oz爆米花+16oz可乐'
        },
        isKefuStyle: ''
      }
    },
    created() {
      this.Indicator.open();
    },
    computed: mapState({
      user: state => state.user.user,
      consume: state => state.user.consume,
      machine_no: state => state.user.machine_no,
      info: state => state.user.info,
      userProgress: state => state.user.user_progress,
    }),
    components: {
      joPay,
      quanprogress,
      mtSwipe: Swipe,
      mtSwipeItem: SwipeItem,
      barcode:vueBarcode
    },
    mounted() {
      this.$store.dispatch('judgeMachine').then((res) => {
        switch (res.site_id) {
          case 50 :
            this.cinemaInfo.name = '北京耀莱影城马连道店';
            break;
          case 5 :
            this.cinemaInfo.name = 'Jackie Chan北京耀莱';
            break;
          case 196 :
            this.cinemaInfo.name = '北京耀莱成龙国际影城西红门店';
            break;
          case 91 :
            this.cinemaInfo = {name: '完美世界影城', price: 18, goodsName: '32oz爆米花+12oz可乐'};
            break;
        }
      })
      this.$store.dispatch('getUser')
        .then(() => {
          this.Indicator.close()
          this.showHtml = true
        });

    },
    methods: {
      getVoucherLength(value){
        if(value>1){
        var centerMain= document.querySelector('.center-main');
        centerMain.style = 'width:'+5.91*value+'rem';
          var center = document.querySelector('.centeres');
          center.style = 'width:6.15rem;padding:0 0 0 0.15rem';
        }
      },
      addEventScroll(event){
        var coupon = document.querySelector('.coupon');
        this.pageIndex = Math.round( event.target.scrollLeft/coupon.clientWidth)+1;
      },
      handleChange(index) {
        this.pageIndex = index + 1;
      },
      openTip(value) {
        this.bgShow = true;
        this.contentShow = value;
      },
      changeTip() {
        this.bgShow = true;
        this.contentShow = 'infinity';
        this.is_lamp_after = true
        setTimeout(() => {
          this.is_lamp_after = false
        }, 60000)
      },
      closeBg() {
        this.bgShow = false;
      },
      useCoupons(coupon_id, coin_price_id, coupon_type, coin, type) {
        this.$refs.operations.useCoupons(coupon_id, coin_price_id, coupon_type, coin, type);
      },
      handlerGameNum(x) {
        if (x === '-') {
          if (this.gameNum === 1) return false;
          this.gameNum--;
          _hmt.push(['_trackEvent', '减币', '点击', '', '']);
        } else {
          if (((this.gameNum + 1) * this.info.coin_num) > (this.user.coins + this.info.coin_num * this.user.game_ticket)) {
            if (this.user.coins < this.info.coin_num) this.openRecharge('recharge')
            return false
          }
          this.gameNum++;
          _hmt.push(['_trackEvent', '加币', '点击', '', '']);
        }
      },
      //投币，开始游戏
      handleStartingDevice() {
        if (this.info.online == 0) {
          MessageBox.alert('设备离线请扫码更换机器', '提示').then(() => {
            this.handleScanQRCode();
          });
          return
        }
        if (this.is_start) {
          return
        } else {
          this.is_start = true
          this.is_lamp_after = false
          this.start_desc = '投币中'
          if (this.user.game_ticket + this.user.coins < this.info.coin_num) {
            this.start_desc = '';
            this.is_start = false
            MessageBox.alert('余额不足，请充值', '提示').then(() => {
            });
            return false
          }
          //添加百度统计
          _hmt.push(['_trackEvent', '主按钮投币', '点击', '投币-游戏次数-' + this.gameNum, '']);
          this.$store.dispatch('startingDevice', this.gameNum * this.info.coin_num)
            .then(() => {
              this.is_lamp_after = true
              this.is_start = false
              this.start_desc = '投币启动'
              this.$refs.quanprogress.getFallback(this.gameNum);
              this.gameNum = 1;
              setTimeout(() => {
                this.is_lamp_after = false
              }, 12000 * this.gameNum)
              //点击投币后调用
            })
            .catch(() => {
              this.is_start = false
              this.start_desc = '投币启动'
            })
        }
      },
      handleScanQRCode() {
        _hmt.push(['_trackEvent', '扫码更换机器', '点击', '', '']);//添加百度统计
        if (CONFIG.isAlipay) {
          ap.scan(function (res) {
            window.location.href = res.code
          })
        } else {
          wx.scanQRCode({
            needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
            scanType: ["qrCode"], // 可以指定扫二维码还是一维码，默认二者都有
            success: function (res) {
              var result = res.resultStr; // 当needResult 为 1 时，扫码返回的结果
              window.location.href = result
            },
            error: function () {
              alert('扫码失败')
            }
          })
        }
      },
//      support() {
//        _hmt.push(['_trackEvent', '客服', '点击', '', '']);
//      },
      showKefu() {
        _hmt.push(['_trackEvent', '客服', '点击', '', '']);
        this.isKefuStyle = ' transform: translate3d(0,0,0);';
      },
      hideKefu() {
        this.isKefuStyle = ' transform: translate3d(100%,0,0);';
      }

    },
    filters: {
      handelDate(value) {
        var data = value.split(' ')[0].split('-');
        return data[0] + '年' + data[1] + '月' + data[2] + '日';
      },
      addSpace(value){
        var reg = /(.{4})/g;
        return value.replace(reg,'$1'+' ');
      }
    }
  }
</script>

